import { useState } from "react";
import "./styles.css";
var food_obj = {
  "üç≤": "pot of food",
  "ü•´": "Canned Food",
  "ü•ò": "Shallow Pan of Food",
  "ü•°": "Take out box",
  "üßÜ": "Falafel",
  "üçõ": "Curry rice",
  "ü¶ê": "Shrimp"
};

var foodipedia = Object.keys(food_obj);
export default function App() {
  const [uservalue, setvalue] = useState("");

  function foodInputHandler(event) {
    var userinput = event.target.value;
    var uservalue = food_obj[userinput];

    if (uservalue === undefined) {
      uservalue = "Oops! Not in our DataBase";
    }
    setvalue(uservalue);
  }

  function emojiClickHandler(emoji) {
    var uservalue = food_obj[emoji];
    setvalue(uservalue);
  }

  return (
    <div className="App">
      <h1>Food emoji Interpreter</h1>
      <input onChange={foodInputHandler}></input>
      <h2> {uservalue} </h2>
      <h3>Food emojis, we know</h3>
      {foodipedia.map(function (emoji) {
        return (
          <li onClick={() => emojiClickHandler(emoji)} key={emoji}>
            {emoji}
          </li>
        );
      })}
    </div>
  );
}
